<template>
	<v-card class="ma-md-6 ma-sm-3">
		<v-card-title class="py-2">
			<v-icon class="mr-3">mdi-file-outline</v-icon>
			Edit Newsletter
		</v-card-title>
		<v-divider></v-divider>
		<v-form ref="form" v-model="valid">
			<v-card-text>		
				<div class="title">Judul</div>	
				<v-text-field
					outline
					outlined
					dense
					v-model="dataBerita.judul"
					:rules="requiredRule"
				></v-text-field>	
				<div class="title">Kategori</div>	
				<v-select
					outline
					outlined
					dense
					:rules="requiredRule"
					:items="dataKategori"
					item-text="nama"					
					item-value="id"	
					v-model="dataBerita.kategori.kategori_id"				
				></v-select>
				<div class="title">Artikel</div>	
				<VueCkeditor v-model="dataBerita.konten" />
        <br>
        <Notif :msg="msg" :status="status" @visible="visible" />          
			</v-card-text>
			<v-divider></v-divider>
			<v-card-actions>
				<v-spacer></v-spacer>
				<v-btn
					depressed
					type="submit"
					:disabled="!valid"
					:loading="isLoading"
					@click.prevent="validate"
					color="success">
					publish
				</v-btn>
			</v-card-actions>
		</v-form>
	</v-card>
</template>

<script>
  import { mapState } from 'vuex'
  import Notif from '@/components/Notif'
	import VueCkeditor from '@/components/Berita/VueCkeditor'

	export default{
		props: ['id'],

		components: {
			VueCkeditor
		},

		data: () => ({
			valid: true,
			requiredRule: [v => !!v || 'Data harus di isi'],
			msg: {
        success: 'Data berhasil ditambahkan',
        error: 'Data gagal ditambahkan',
        visible: false
      }
		}),

    components: { Notif, VueCkeditor },

    created() {
    	this.$store.dispatch('dataBerita/getDetailBerita', this.id)
    	this.$store.dispatch('dataKategori/getDataKategori')
    },

    computed: { 
    	...mapState('dataBerita', ['status', 'isLoading', 'dataBerita']), 
    	...mapState('dataKategori', ['dataKategori']),
    	detailKategori: {
    		get() {
    			return this.dataKategori
    		},
    		set(val) {
    			this.dataKategori.filter(x => x.id === this.dataBerita.kategori.kategori_id)
    		}
    	}
    },

    methods: {
      validate () {
        if (this.$refs.form.validate()) {
        	const data = {
        		id: this.dataBerita.id,
        		judul: this.dataBerita.judul,
        		kategori_id: this.dataBerita.kategori.kategori_id,
        		konten: this.dataBerita.konten
        	}
          this.$store.dispatch('dataBerita/putDataBerita', data)
          this.$store.dispatch('dataBerita/updateIsLoading', true)
          this.msg.visible = true
        }
      },
      visible(val) {
        this.msg.visible = val
      }
    }
	}
</script>